import { cn } from '@/lib/utils';

interface Props {
    text: string;
    className?: string;
}

const MainHeading = ({ text, className }: Props) => {
    return (
        <div
            className={cn(
                'w-full text-primery-blue-500 border-b border-primery-blue-500 pb-[5.5px] font-bold text-[22px] leading-[33px] tracking-normal text-center mb-4',
                className,
            )}
        >
            {text}
        </div>
    );
};

export default MainHeading;
